.home{
  display: grid;
  grid-template-columns: 1.5fr 1fr 1fr 1fr;
  grid-template-rows: 140px 140px 527px;
  grid-gap: 1rem;
  gap: 1rem;
  margin: 119px 47px 36px 47px;
  /* background: blue; */
  
}
.cardCalender{
  background-color: white;
  border-radius: 10px;
  grid-row: 1/3;
  margin-right: 1rem;
  padding-top: 20px;
  padding-left: 20px;
}
.cardStat{
  background-color: white;
  border-radius: 10px;
  /* padding: 42px 24px 42px 24px; */
}
.cardStat img{
  float: right;
  margin-right: 24px;
  margin-top: 50px;
}
.cardBottom{
  background-color: white;
  border-radius: 10px;
  grid-row: 3/4;
  grid-column: 1/2;
  margin-right: 1rem;
  padding: 17px;
  font-size: 20px;
}
.cardBottomRight{
  background-color: white;
  border-radius: 10px;
  grid-row: 3/4;
  grid-column: 2/5;
  margin-right: 1rem;
  padding: 17px;
  font-size: 20px;
}
.cardStat h4{
  margin-top: 42px;
  margin-left: 24px;
  font-style: normal;
  font-weight: 500;
  font-size: 39.06px;
  line-height: 47px;
  
  color: #102A43; 
}
.cardStat p{
  margin-left: 24px;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  
  color: #17537F; 
}
.navbar{
    background: linear-gradient(180deg, #56CCF2 0%, rgba(47, 128, 237, 0) 100%), #2680C2;
    height: 80px;
    display: flex;
    justify-content: flexstart;
    align-items: center;
}

.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}
.nav-menu{
    background: linear-gradient(180deg, #56CCF2 0%, rgba(47, 128, 237, 0) 100%), #2680C2;
    width: 260px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active{
    left: 0;
    transition: 350ms;
}
.nav-text{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 34px;
    list-style: none;
    height: 69px;
}
.nav-text-active{
    background-color: white;
}
.roseFam{
    margin-left: 27px;
    margin-top: 34px;
    margin-bottom: 40px;
    list-style: none;
    font-size: 32px;
    line-height: 38px;
    color: #F0F4F8;
}
.nav-text a{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 10px 0px 0px 10px;
    transition: .5s;
}
.nav-text a:hover{
    background: #4389b8;
    /* color: #003E6B !important; */
}
.clicked{
    color: #003E6B;
    background-color: #E5E5E5;
}
.clicked:hover{
    background: #E5E5E5 !important;
}

.clicked > span{
    color: #003E6B;
}

.nav-menu-items{
    width: 100%;
}
.navbar-toggle{
    background: linear-gradient(180deg, #56CCF2 0%, rgba(47, 128, 237, 0) 100%), #2680C2;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.dot{
    width: 20px;
    height: 20px;
    /* background-image: url("../img/Ellipse1.png");     */
    border-style: none;
}
span{
    margin-left: 16px;
}
